<template>
<div class="price">
    <h1 class="price__title">아트홀 요금</h1>

    <div class="price__introduce">
        <div class="price__container">
            <div class="price__swipe">
                <div class="single-piano piano-default">
                    <h2 class="piano-title">SINGLE PIANO</h2>
                    <p class="piano-price">￦ <strong>{{ singlePrice }}</strong></p>
                    <p class="per-hour">(시간 당)</p>
                </div>

                <div class="double-piano piano-default">
                    <h2 class="piano-title">DOUBLE PIANO</h2>
                    <p class="piano-price">￦ <strong>{{ doublePrice }}</strong></p>
                    <p class="per-hour">(시간 당)</p>
                </div>
            </div>
            <span class="swipe__indigator">
                <span class="active"></span>
                <span></span>
            </span>
        </div>

        <div class="price__info">
            <p class="piano-tunning">숲 아트홀의 피아노는 30년 이상의 경력을 가진 전속 조율사가 월 1회 이상 정기적으로 조율합니다.<br v-if="tuneBoolean" /> 연주 전 조율이 필요할 시 숲 아트홀 전속 조율사의 조율을 받아야 하며, 조율비가 추가됩니다.</p>
            <p class="refund-standard">
                <strong>[ 환불 기준 ]</strong><br />
                <span>
                    방문 3일전 : 총 금액의 100% 환불<br />
                    방문 1-2일전 : 총 금액의 50% 환불<br />
                    방문 당일 : 총 금액의 10% 환불
                </span>
            </p>
            <p class="warring">
                ※ 숲 아트홀은 24시간 운영하며, 예약 후 사용을 원칙으로 합니다.<br />
                ※ 최소 대여 시간은 1시간이며, 30분 단위로 추가할 수 있습니다.
            </p>
        </div>
    </div>
</div>
</template>

<script>
import gsap from 'gsap';

export default {
    name: 'Price',
    data() {
        return {
            singlePrice: '60,000',
            doublePrice: '100,000',
            tuneBoolean: true,
        }
    },
    mounted() {
        this.tuneStatus();
        window.addEventListener('resize', this.tuneStatus);
    },
    methods: {
        tuneStatus() {
            if (window.innerWidth < 600) {
                this.tuneBoolean = false;
            } else {
                this.tuneBoolean = true;
            }

            if(window.innerWidth <= 460) {
                this.priceSwipe();
            }
        },
        priceSwipe() {
            let swipeContainer = document.querySelector('.price__container'),
            swipeObject = document.querySelector('.price__swipe'),
            swipeIndigator = document.querySelectorAll('.swipe__indigator span');
            var startOffset = 0, moveOffset = 0, endOffset = 0, swipeIdx = 1;

            swipeContainer.addEventListener('touchstart', swipeStart);
            swipeContainer.addEventListener('touchmove', swipeMove);
            swipeContainer.addEventListener('touchend', swipeEnd);

            function swipeStart(e) {
                startOffset = e.changedTouches[0].clientX;
            }

            function swipeMove(e) {
                moveOffset = e.changedTouches[0].clientX;

                if((startOffset - moveOffset) < startOffset ){
                    // gsap.to(swipeObject)
                    // console.log(startOffset - moveOffset);
                }
            }

            function swipeEnd(e) {
                endOffset = e.changedTouches[0].clientX;

                if(startOffset < endOffset && Math.abs(startOffset - endOffset) >= 50 && swipeIdx == 2){
                    gsap.to(swipeObject, {xPercent: 0, duration: 0.5});
                    swipeIdx = 1;
                }else if(startOffset > endOffset && Math.abs(startOffset - endOffset) >= 50 && swipeIdx == 1){
                    gsap.to(swipeObject, {xPercent: -100, duration: 0.5});
                    swipeIdx = 2;
                }

                swipeIndigator.forEach((el) => {
                    el.classList.remove('active');
                })
                swipeIndigator[swipeIdx - 1].classList.add('active');
            }
            
        }
    }
}
</script>

<style lang="scss">
.price {
    width: 100%;
    padding: 100px 0 150px 0;
    color: $mainColor;
}

.price__title {
    font-size: $fs36;
    text-align: center;
    padding-bottom: 50px;
}

.price__introduce {
    width: 800px;
    margin: 0 auto;
    box-shadow: 0 0 8px 0.5px rgba(102, 102, 102, 0.2);
    border-radius: 10px;
    overflow: hidden;
}

.price__container {
    width: 100%;
    position: relative;
    background: #f2f0e8;

    &::before,
    &::after {
        content: '';
        display: inline-block;
        position: absolute;
        background: #ddd;
    }

    &::before {
        width: 1px;
        height: 100%;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
    }

    &::after {
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
    }
}

.swipe__indigator{
    display: none;
    justify-content: space-between;
    height: 7px; width: 21px;
    position: absolute;
    left: 50%; bottom: 20px;
    transform: translateX(-50%);

    span{
        display: inline-block;
        width: 7px; height: 100%;
        background: #fff;
        border-radius: 100%;
        transition: .5s;

        &.active{
            background: $mainColor;
        }
    }
}

.price__swipe{
    width: 100%;
    display: flex;
}

.piano-default {
    width: 50%;
    text-align: center;
    padding: 50px 0;
}

.piano-title {
    font-size: $fs24;
    padding-bottom: 20px;
    letter-spacing: 0;
}

.piano-price {
    padding-bottom: 5px;
    font-size: $fs14;

    strong {
        font-size: $fs24;
        letter-spacing: 0;
    }
}

.per-hour {
    font-size: $fs14;
}

.price__info {
    padding: 40px 20px;
    text-align: center;
    font-size: $fs16;
}

.piano-tunning {
    padding-bottom: 30px;
    color: #b2b2b2;
    line-height: 1.5;
}

.refund-standard {
    line-height: 2;
    padding-bottom: 50px;
}

.warring {
    text-align: left;
    display: inline-block;
    font-size: $fs14;
    color: #b2b2b2;
    line-height: 1.5;
}

@media (max-width: 840px) {
    .price {
        padding: 100px 20px 150px 20px;
    }

    .price__introduce {
        width: 100%;
    }
}

@media (max-width: 720px) {
    .price__info {
        font-size: $fs14;
    }

    .warring {
        font-size: $fs12;
    }
}

@media (max-width: 600px) {
    .piano-tunning {
        text-align: left;
    }
}

@media (max-width: 460px) {
    .price__container {
        white-space: nowrap;

        &::before {
            display: none;
        }
    }

    .price__swipe{
        display: block;
    }

    .swipe__indigator{
        display: flex;
    }

    .piano-default {
        width: 100%;
        display: inline-block;
    }
}
</style>
